<template>
  <div class="message-container" ref="messageContainer">
    <MessageArea
      title="留言板"
      :subTitle="`(${data.total})`"
      :list="data.rows"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea";
import fetchData from "@/mixins/fetchData.js";
import mainScroll from "@/mixins/mainScroll";
import * as msgApi from "@/api/message";
export default {
  mixins: [fetchData({ total: 1, rows: [] }), mainScroll("messageContainer")],
  components: {
    MessageArea,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  created() {
    // 调用事件总线监听事件
    this.$bus.$on("mainScroll", this.handleScroll);
  },
  destroyed() {
    // 解除事件监听
    this.$bus.$off("mainScroll", this.handleScroll);
  },
  computed: {
    //计算属性
    hasMore() {
      //查看是否还有更多评论
      return this.data.rows.length < this.data.total;
    },
  },
  methods: {
    async fetchData() {
      return await msgApi.getMessages(this.page, this.limit);
    },
    async handleSubmit(data, callback) {
      const resp = await msgApi.postMessage(data);
      callback("感谢您的留言");
      this.data.total++; //评论数加一
      this.data.rows.unshift(resp); //把发送添加评论返回的值放入数据中
    },
    handleScroll(dom) {
      if (this.isLoading || !dom) {
        //判断是否还有数据如果没有了就跳出
        //或者dom已经销毁的话就不用在进行事件了
        // 目前正在加载更多
        return;
      }
      const range = 100; // 顶一个可接受的范围，在这个范围内都算达到了底部
      const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      if (dec <= range) {
        this.fetchMore();
      }
    },
    // 加载下一页
    async fetchMore() {
      if (!this.hasMore) {
        // 没有更多啦
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
  },
};
</script>

<style scoped lang="less">
.message-container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 25px 0px;
  box-sizing: border-box;
  scroll-behavior: smooth;
  .message-area-container {
    width: 700px;
    margin: 0 auto;
  }
}
</style>
